* {
  margin: 0;
  padding: 0;
}

html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #000;
}

.logo_img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_list {
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  cursor: pointer;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}
